import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

class Square extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      value : null,
    };
  }

  render() {
    return (
      <button
        className="square"
        onClick = {() => {
          this.props.onClick()
        }}
      >
        {this.props.value}
      </button>
    );
  }
}


class Board extends React.Component {
  constructor(props){
    super(props);

    let r = Math.round(Math.random());
    let nt = null;
    let it = null;
    if(r === 0){
      nt = 'O';
      it = 'X';
    }
    else{
      nt = 'X';
      it = 'O';
    } 


    this.state = {
      game_info : {
        next_state : nt,
        iter_state : it,
        historia : [],
        board_states : [null, null, null, null, null, null, null, null, null],
        winner : null,
      }
    };
  }

  handleClick(i) {
    let pivot_game = this.state.game_info;
    let pivot_hist = pivot_game.historia
    let pivot_board = pivot_game.board_states;
    let pivot_state = pivot_game.iter_state;

    pivot_hist.push(pivot_board);
    pivot_board[i] = pivot_game.next_state;
    pivot_game.iter_state = pivot_game.next_state;
    pivot_game.next_state = pivot_state;

    this.setState({
      game_info : pivot_game
    });
  }

  checkGanador() {
    const cells = this.state.game_info.board_states;
    // horizontal
    if(cells[0] === cells[1] && cells[1] === cells[2] && cells[0] != null){
      return <div className='game-result'>Jugador ganador {cells[0]}</div>;
    }
    if(cells[3] === cells[4] && cells[4] === cells[5] && cells[3] != null){
      return <div className='game-result'>Jugador ganador {cells[3]}</div>;
    }
    if(cells[6] === cells[7] && cells[7] === cells[8] && cells[6] != null){
      return <div className='game-result'>Jugador ganador {cells[6]}</div>;
    }

    // vertical
    if(cells[0] === cells[3] && cells[3] === cells[6] && cells[0] != null){
      return <div className='game-result'>Jugador ganador {cells[0]}</div>;
    }
    if(cells[1] === cells[4] && cells[4] === cells[7] && cells[1] != null){
      return <div className='game-result'>Jugador ganador {cells[1]}</div>;
    }
    if(cells[2] === cells[5] && cells[5] === cells[8] && cells[2] != null){
      return <div className='game-result'>Jugador ganador {cells[2]}</div>;
    }

    // cross
    if(cells[0] === cells[4] && cells[4] === cells[8] && cells[0] != null){
      return <div className='game-result'>Jugador ganador {cells[0]}</div>;
    }
    if(cells[2] === cells[4] && cells[4] === cells[6] && cells[2] != null){
      return <div className='game-result'>Jugador ganador {cells[2]}</div>;
    }
    // default return
    return <div className='game-result'>-</div>;
  }

  renderSquare(i, state) {
    return <Square
      value={state}
      id={i}
      onClick={() => this.handleClick(i)}
      />;
  }

  render() {

    return (
      <div>
        <div className="status">Pr√≥ximo jugador: {this.state.game_info.next_state}</div>
        <div className="board-row">
          {this.renderSquare(0,this.state.game_info.board_states[0])}
          {this.renderSquare(1,this.state.game_info.board_states[1])}
          {this.renderSquare(2,this.state.game_info.board_states[2])}
        </div>
        <div className="board-row">
          {this.renderSquare(3,this.state.game_info.board_states[3])}
          {this.renderSquare(4,this.state.game_info.board_states[4])}
          {this.renderSquare(5,this.state.game_info.board_states[5])}
        </div>
        <div className="board-row">
          {this.renderSquare(6,this.state.game_info.board_states[6])}
          {this.renderSquare(7,this.state.game_info.board_states[7])}
          {this.renderSquare(8,this.state.game_info.board_states[8])}
        </div>
        {this.checkGanador()}
        <div>{console.log(this.state.game_info.board_states)}</div>
      </div>
    );
  }
}


class Game extends React.Component {
  render() {
    return (
      <div className="container">
        <div className="game">
          <div className="game-board">
            <Board />
          </div>
          <div className="game-info">
            <div>{/* status */}</div>
            <ol>{/* TODO */}</ol>
          </div>
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Game />,
  document.getElementById('root')
);